<template>
	<div id="app">
		<div id="top_nav">
			<div class="top_nav_left">
				<img src="./assets/img/智信置家.png" style="height: 100%">
			</div>
			<div class="top_nav_right">
				<el-menu :default-active="active_index" mode="horizontal"
						 active-text-color="#409eff">
					<el-menu-item index="1" @click="change_menu('1')">首页</el-menu-item>
					<el-menu-item index="2" @click="change_menu('2')">关于</el-menu-item>
					<el-menu-item index="3" @click="change_menu('3')">案例</el-menu-item>
					<el-menu-item index="4" @click="change_menu('4')">资讯</el-menu-item>
					<el-menu-item index="5" @click="change_menu('5')">联系我们</el-menu-item>
				</el-menu>
			</div>
		</div>
		<router-view/>
	</div>
</template>

<script>

export default {
	name: 'App',
	data() {
		return {
			active_index: '1',
		}
	},
	methods: {
		change_menu: function (index) {
			var _self = this;
			if (index == '1'){
				_self.$router.push('/')
			}
			if (index == '2'){
				_self.$router.push('/about')
			}
			if (index == '3'){
				_self.$router.push('/case')
			}
			if (index == '4'){
				_self.$router.push('/advisory')
			}
			if (index == '5')
				_self.$router.push('/aboutus')
			sessionStorage.setItem('active', index)
			_self.active_index = index
		}
	},
	mounted() {
		var _self = this;
		if (sessionStorage.getItem('active') != null)
			_self.active_index = sessionStorage.getItem('active')
	}
}
</script>
<style scoped>
@import "./assets/css/my_style.css";
</style>
<style>
body {
	width: 1920px;
	height: 1080px;
	overflow: auto;
	margin: 0;
	padding: 0;
}

@font-face {
	/* 汉仪菱心体简 */
	font-family: MyFontName;
	src: url("assets/font/汉仪菱心体简.ttf");
}

#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
}

</style>



