						<img src="../assets/img/mai_2.png">
						<div><h3 style="color: white">强势赋能</h3></div>
						<div class="card_box_div" style="color: white"><span>爆点营销</span></div>
						<div class="card_box_div" style="color: white"><span>亮点大&nbsp;&nbsp;卖点足</span></div>
						<div class="card_box_div" style="color: white"><span>渠道广&nbsp;&nbsp;客户多</span></div>
