					</template>
					<template v-else>
						<img src="../assets/img/load_2.png">
						<div><h3 style="color: white">另辟蹊径</h3></div>
						<div class="card_box_div" style="color: white"><span>开发商的成本不变</span></div>
						<div class="card_box_div" style="color: white"><span>业主获得极大优惠</span></div>
						<div class="card_box_div" style="color: white"><span>解决客户所有问题</span></div>
					</template>
