				<el-card shadow="hover" class="card_box" @mouseenter.native="enter_event('3')" @mouseleave.native="leave_event('3')">
					<template v-if="!is_enter_3">
						<img src="../assets/img/shan_1.png">
						<div><h3>速战速决</h3></div>
						<div class="card_box_div"><span>库存快速去化</span></div>
						<div class="card_box_div"><span>成本降低</span></div>
						<div class="card_box_div"><span>极大降低风险成本</span></div>
					</template>
					<template v-else>
						<img src="../assets/img/shan_2.png">
						<div><h3 style="color: white">速战速决</h3></div>
						<div class="card_box_div" style="color: white"><span>库存快速去化</span></div>
						<div class="card_box_div" style="color: white"><span>成本降低</span></div>
						<div class="card_box_div" style="color: white"><span>极大降低风险成本</span></div>
					</template>
				</el-card>
				<el-card shadow="hover" class="card_box" @mouseenter.native="enter_event('4')" @mouseleave.native="leave_event('4')">
					<template v-if="!is_enter_4">
