			</div>
			<div style="width: 1920px;font-size: 25px;color: #C0C4CC;font-family: SimSun-ExtB" align="center">
				<span>Five Advantages·Empowering You</span>
			</div>
			<div style="width: 1920px;height: 120px"></div>
			<div style="width: 1920px;height: 665px">
				<el-carousel trigger="click" height="665px">
					<el-carousel-item>
						<img src="../assets/img/carousel_1.jpg">
					</el-carousel-item>
					<el-carousel-item>
						<img src="../assets/img/carousel_2.jpg">
					</el-carousel-item>
					<el-carousel-item>
						<img src="../assets/img/carousel_3.jpg">
					</el-carousel-item>
					<el-carousel-item>
						<img src="../assets/img/carousel_4.jpg">
					</el-carousel-item>
					<el-carousel-item>
						<img src="../assets/img/carousel_5.jpg">
					</el-carousel-item>
				</el-carousel>
			</div>
			<div style="width: 1920px;height: 120px"></div>
			<div style="width: 1920px;height: 60px" align="center">
				<div style="display: inline-block;width: 300px;height: 60px;line-height: 60px">
					<span class="span_box current_choose" @click="change_image(1, $event)">活动展示</span>
				</div>
				<div style="display: inline-block;width: 300px;height: 60px;line-height: 60px">
					<span class="span_box" @click="change_image(2, $event)">案例落地</span>
				</div>
				<div style="display: inline-block;width: 300px;height: 60px;line-height: 60px">
					<span class="span_box" @click="change_image(3, $event)">合作流程</span>
				</div>
			</div>
			<div style="width: 1920px;height: 50px"></div>
			<div style="width: 1920px;">
				<img v-if="current_image == 1" src="../assets/img/2.png">
				<img v-if="current_image == 2" src="../assets/img/3.png">
				<img v-if="current_image == 3" src="../assets/img/liucheng.png">
			</div>
			<div style="width: 1920px;height: 120px"></div>
			<div style="width: 1920px;height: 70px;line-height: 70px;font-size: 48px;color: black" align="center">
				<span ><img src="../assets/img/left_'.png">战略合作品牌<img src="../assets/img/right_'.png"></span>
			</div>
			<div style="width: 1920px;font-size: 25px;color: #C0C4CC;font-family: SimSun-ExtB" align="center">
				<span>Strategic Cooperation Brand</span>
			</div>
			<div style="width: 1920px;height: 120px"></div>
			<div style="width: 1920px;">
				<img src="../assets/img/jiameng.png">
