</template>

<script>
export default {
	data() {
		return {
			is_enter_1: false,
			is_enter_2: false,
			is_enter_3: false,
			is_enter_4: false,
			current_image: 1,
		}
	},
	name: "Home",
	methods: {
		enter_event: function (index) {
			var _self = this;
			if (index == '1')
				_self.is_enter_1 = true
			if (index == '2')
				_self.is_enter_2 = true
			if (index == '3')
				_self.is_enter_3 = true
			if (index == '4')
				_self.is_enter_4 = true
		},
		leave_event: function (index) {
			var _self = this;
			if (index == '1')
				_self.is_enter_1 = false
			if (index == '2')
				_self.is_enter_2 = false
			if (index == '3')
				_self.is_enter_3 = false
			if (index == '4')
				_self.is_enter_4 = false
		},
		change_image: function (index, event) {
			var _self = this;
			_self.current_image = index
			let span_box = document.getElementsByClassName('span_box');
			for (let i = 0; i < span_box.length; i++) {
				if (span_box[i].classList.contains('current_choose'))
					span_box[i].classList.remove('current_choose')
			}
			var current_div = event.currentTarget
			current_div.classList.add('current_choose')
		}
	}
}
</script>

<style scoped>
@import "../assets/css/my_style.css";
</style>
<style>
.el-divider--horizontal {
	height: 3px !important;
	width: 32px !important;
	margin-left: 49% !important;
}
</style>



