import {AMapManager, lazyAMapApiLoaderInstance} from 'vue-amap'

let amapManager = new AMapManager()
let amapManager_2 = new AMapManager()
export default {
	name: "AboutUs",
	data() {
		var _self = this;
		return {
			center: [112.581346, 37.753437],
			center_2: [112.593698, 37.676026],
			zoom: 17,
			zoom_2: 17,
			amapManager,
			amapManager_2,
			lng: 0,
			lat: 0,
			loaded: false,
			label: {
				content: "智信智家",
				offset: [10, 12]
			},
			label_2: {
				content: "智信智家仓库",
				offset: [10, 12]
			},
			events: {
				init() {
					lazyAMapApiLoaderInstance.load().then(() => {
						_self.initSearch()
					})
				},
				// 点击获取地址的数据
				click(e) {
					// console.log(e)
					self.markers = []
					let {lng, lat} = e.lnglat
					self.lng = lng
					self.lat = lat
					self.center = [lng, lat]
					self.markers.push([lng, lat])
					// 这里通过高德 SDK 完成。
					let geocoder = new AMap.Geocoder({
						radius: 1000,
						extensions: 'all'
					})
					geocoder.getAddress([lng, lat], function (status, result) {
						if (status === 'complete' && result.info === 'OK') {
							if (result && result.regeocode) {
								console.log(result.regeocode.formattedAddress)
								self.address = result.regeocode.formattedAddress
								self.searchKey = result.regeocode.formattedAddress
								self.$nextTick()
							}
						}
					})
				}
			},
			events_2: {
				init() {
					lazyAMapApiLoaderInstance.load().then(() => {
						_self.initSearch_2()
					})
				},
				// 点击获取地址的数据
				click(e) {
					// console.log(e)
					self.markers = []
					let {lng, lat} = e.lnglat
