				// {
				//   pName: 'Geocoder',
				//   events: {
				//     init (o) {
				//       console.log(o.getAddress())
				//     }
				//   }
				// },
				{
					// 定位
					pName: 'Geolocation',
					enableHighAccuracy: false,//是否使用高精度定位，默认:true
					timeout: 100,          //超过10秒后停止定位，默认：无穷大
					maximumAge: 0,           //定位结果缓存0毫秒，默认：0
					convert: true,           //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
					showButton: true,        //显示定位按钮，默认：true
					buttonPosition: 'RB',    //定位按钮停靠位置，默认：'LB'，左下角,,,'RB'右下角
					showMarker: false,        //定位成功后在定位到的位置显示点标记，默认：true
					showCircle: false,        //定位成功后用圆圈表示定位精度范围，默认：true
					panToLocation: false,     //定位成功后将定位到的位置作为地图中心点，默认：true
					zoomToAccuracy: false,//定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：f
					extensions: 'all',
					events: {
						init(o) {
							// o是高德地图定位插件实例
							o.getCurrentPosition((status, result) => {
								if (result && result.position) {
									// 设置经度
									_self.lng = _self.center[0]
									// 设置维度
									_self.lat = _self.center[1]
									// 设置坐标
									// _self.center = [_self.lng, _self.lat]
									// _self.markers.push([_self.lng, _self.lat])
									// load
									_self.loaded = true
									// 页面渲染好后
									_self.$nextTick()
								}
							})
						}
					}
				},
