					// 鹰眼
					pName: 'OverView',
					events: {
						init(instance) {
							// console.log(instance);
						}
					}
				},
				{
					// 地图类型
					pName: 'MapType',
					defaultType: 0,
					events: {
						init(instance) {
							// console.log(instance);
						}
					}
				},
				{
					// 搜索
					pName: 'PlaceSearch',
					events: {
						init(instance) {
