	}
	,
	methods: {
		initSearch() {
			let vm = this
			let map = this.amapManager.getMap()
			// map.add(vm.markers)
		},
		initSearch_2() {
			let vm = this
			let map = this.amapManager_2.getMap()
			// map.add(vm.markers)
		},
		searchByHand() {
			if (this.searchKey !== '') {
				this.poiPicker.searchByKeyword(this.searchKey)
			}
		}
	}
}
</script>

<style scoped>
@import "../assets/css/my_style.css";
</style>
<style>
.el-card__body {
	padding: 0 !important;
}

.el-textarea {
	border-bottom: 10px solid #eeeeee;
	border-top: 10px solid #eeeeee;
	border-left: 50px solid #eeeeee;
	border-right: 50px solid #eeeeee;
}

.el-input {
	border-bottom: 2px solid #eeeeee;
	border-top: 2px solid #eeeeee;
	border-left: 50px solid #eeeeee;
	border-right: 50px solid #eeeeee;
}
</style>



